@font-face {
  font-family: 'Inter';
  font-display: swap;
  src: url(/vcom_dashboard/static/media/inter-400-regular.41e86578.woff);
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto';
  src: url(/vcom_dashboard/static/media/roboto-400-regular.60fa3c06.woff);
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto';
  src: url(/vcom_dashboard/static/media/roboto-500.87284894.woff);
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto';
  src: url(/vcom_dashboard/static/media/roboto-900.bb1e4dc6.woff);
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto Slab';
  src: url(/vcom_dashboard/static/media/roboto-slab-400-regular.28fdd9b8.woff);
  font-weight: 400;
  font-style: normal;
}

.header{
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  padding-left: 40px;
  padding-right: 40px;
  align-items: center;
  height: 80px;
  border-bottom: solid 1px #eeeeee;
  box-shadow: 0 1px 8px rgb(0 0 0 / 0.1);
}

.header__heading{
  font-family: 'Roboto Slab', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 22px;
  line-height: 24px;
  color: #0f5edb;
}


.nav__menu{
  display: flex;
  flex-direction: row;
  grid-gap: 50px;
  gap: 50px;
  padding: 0;
  list-style: none;
}

.nav{
  display: flex;
  align-items: center;
}

.nav__link{
  font-family: 'Roboto', sans-serif;
  text-decoration: none;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 20px;
  color: #1A1B22;
  transition-duration: 0.3s;
}


.nav__link:hover{
  color: #0f5edb;
}

.about{
  width: 100%;
  display: flex;
  justify-content: center;
  background: #F5F6F7;
  padding-top: 90px;
  padding-bottom: 100px;
}

.about__container{
  display: flex;
  justify-content: center;

}

.about__content{
  max-width: 40%;
  display: flex;
  flex-direction: column;
  margin-left: 50px;
}


.about__img{
  width: 550px;
  object-fit:cover;
}

.about__heading{
  max-width: 100%;
  font-family: 'Roboto Slab';
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 133%;
  margin: 0;
  color: #0f5edb;
  margin-bottom: 20px;
}

.about__text{
  width: 100%;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 133%;
  color: #1A1B22;
  margin-top: 0;
}

.about__button{
  width: 200px;
  background: #0f5edb;
  border-radius: 50px;
  color: white;
  border: none;
  height: 40px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 133%;
  transition-duration: 0.3s; 
  margin-top: 20px;
  cursor: pointer;
}

.about__button:hover{
  background: #2F71E5;
}

.tools{
  width: 100%;
  height: 550px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #0f5edb;
}      

.tools__container{
  max-width: 100%;
  display: flex;
  grid-gap: 45px;
  gap: 45px;
  margin-bottom: 50px;
}

.tools__title{
  margin:0; 
  margin-bottom: 50px;
  align-self: start;
  padding-left: 40px;
  font-family: 'Roboto Slab';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 133%;
  color: white;
  
}

.card{
  width : 250px;
  height: 300px;
  background: white; 
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px 20px 20px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 4px 12px; 
}

.card__img{
  width: 100px;
  height: 100px;
}

.card__content{
  width: 100%;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}


.card__title{
  width: 100%;
  margin: 0;
  font-family: 'Roboto Slab';
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 133%;
  margin: 0;
  color: #0f5edb;
 
}


.card__desc{
  max-width: 100%;
  margin-bottom: 0;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 133%;
  color: #1A1B22;
 
}


.member{
  background: #F5F6F7;
}

.member__container{
  grid-gap: 100px;
  gap: 100px;
}

.member__title{
  color: #0f5edb;
}

.avatar{
 width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.avatar__img{
  width: 300px;
  height: 280px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px; 
}


.avatar__content{
  max-width:  100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.avatar__title{
  margin: 0;
  margin-top: 20px;
  color: #0f5edb;
  font-family: 'Roboto Slab';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 133%;
 
}

.avatar__major{
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 133%;
  color: #1A1B22;
  margin:0;
}

.footer{
  display: flex;
  max-width: 100%;
  height: 80px;
  background: white;
  align-items: center;
  padding-left: 40px;
  border-top: solid 1px #eeeeee;
  box-shadow: 0 1px 8px rgb(0 0 0 / 0.1);
}

.footer__copyright{
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 133%;
  color: #B0B0B0;
  margin:0;
  
}

.selection{
  width: 100%;
  background: #F5F6F7;
  display: flex;
  justify-content: center;
  padding-bottom: 60px;
}

.selection__container{
  max-width: 100%;
  display: flex;
  grid-gap:100px;
  gap:100px;
}

.selection__heading{
  font-family: 'Roboto Slab';
  font-style: normal;
  font-weight: 300;
  font-size: 30px;
  line-height: 133%;
  margin: 0;
  padding-top: 20px;
  padding-bottom: 40px;
  padding-left: 40px;
  background: #F5F6F7;
  color: #0f5edb;
}

.country__button{
  text-decoration: none;
  height: 200px;
  width: 200px;
  border-radius: 50%;
  border: solid 2px #0f5edb;
  background: none;
  font-family: 'Roboto Slab';
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 133%;
  margin: 0;
  color: #0f5edb;
  transition-duration: 0.4s;
  cursor: pointer;
}

.country__button:hover{
  background: #0f5edb;
  border: solid 2px #0f5edb;
  color: #ffffff;
}

.dashboard{
  width: 100%;
  background: white;
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
}

.dashboard__container{
  width: calc(100% - 100px);
  background: #F5F6F7;
  height: 800px;
}

.dashboard__heading{
  background: white;
  margin-top: 20px;
}

.main__heading{
  background: #F5F6F7;
  padding-top: 50px;
  margin-top: 0;
}

.main{
  background: #F5F6F7;
  margin-bottom: 0;
  padding-bottom: 50px;
  height: auto;
}

.main__container{
  height: auto;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 133%;
  color: #1A1B22;
  margin-top: 0;
}

.docs{
  width: 1200px;
  margin: auto;
}

.docs__section{
  width: 900px;
  margin: auto;
  margin-bottom: 60px
}


.docs__heading{
  font-family: 'Roboto Slab';
  font-style: normal;
  font-weight: 300;
  font-size: 26px;
  line-height: 133%;
  margin: 0;
  background: #F5F6F7;
  color: #0f5edb; 
}

.docs__content{
  margin: 0;
  margin-bottom: 20px;
  margin-top: 20px;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #1A1B22;

}

.docs__img{
  width: 1200px;
  margin: auto;
  background: white;
  margin-bottom: 60px;
}


.docs__img_azure{
  width: 100%;
  margin: auto;
  background: white;
  margin-bottom: 10px;
}

.docs__code{
  background: #ededed;
  color: #8b0000;
}

body {
  margin: 0;
  width : 100%;
  box-sizing: border-box;
}

.report{
  width: 1200px;
  margin: auto;
  font-size: 18px;
  line-height: 133%;
}



